import React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';import { useAuth } from '../../auth/AuthContext';
import Comments from '../../comments/Comments';
import PostHeader from './PostHeader';
import PostContent from './PostContent';
import PostKeywords from './PostKeywords';
import PostAttachments from './PostAttachments';
import PostLinks from './PostLinks';
import usePostData from '../../../hooks/usePostData';
import usePostActions from '../../../hooks/usePostActions';
import AnimatedLoading from '../../common/AnimatedLoading';
import LoadingSpinner from '../../common/LoadingSpinner';

/**
 *    
 *      .
 */
const BasePostView = ({
  collectionName,
  previewData,     
  previewAuthor
}) => {
  const { postId } = useParams();
  const navigate = useNavigate();
  const { currentUser } = useAuth();

  //   
  const {
    postData,
    authorData,
    loading,
    error,
    isLiked,
    likeCount,
    toggleLike,
    fetchLikedUsers,
    getDisplayImage
  } = usePostData(postId, collectionName, previewData, previewAuthor);
  console.log(postData);
  
  //    
  const {
    handleEdit,
    handleDelete,
    deleting
  } = usePostActions(postId, collectionName, postData);

  //   
  const handleAuthorClick = () => {
    if (!authorData?.id) return;
    
    if (currentUser?.uid === authorData.id) {
      navigate('/mypage');
    } else {
      navigate(`/profile/${authorData.id}`);
    }
  };

  //  
  const handleLike = async () => {
    if (!currentUser) {
      alert(' .');
      return;
    }
    
    try {
      await toggleLike();
    } catch (error) {
      alert(error.message);
    }
  };

  //  
  if (loading) {
    return <AnimatedLoading message="  " />;
  }

  //   
  if (error) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Paper sx={{ p: 4, textAlign: 'center' }}>
          <Typography variant="h6" color="error">
            {error}
          </Typography>
        </Paper>
      </Container>
    );
  }

  //  
  if (!postData || !authorData) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Paper sx={{ p: 4, textAlign: 'center' }}>
          <Typography variant="h6">
               .
          </Typography>
        </Paper>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Paper elevation={0} sx={{ borderRadius: 2, overflow: 'hidden' }}>
        {/*  -  , , /  */}
        <PostHeader
          postId={postId}
          postData={postData}
          authorData={authorData}
          isLiked={isLiked}
          likeCount={likeCount}
          isPreview={Boolean(previewData)}
          currentUser={currentUser}
          collectionName={collectionName}
          onDelete={handleDelete}
          onLike={handleLike}
          onAuthorClick={handleAuthorClick}
        />
        
        {/*  - , , ,   */}
        <PostContent
          title={postData.title}
          subtitle={postData.subtitle}
          content={postData.content}
          thumbnailUrl={postData.thumbnail === 'markdown-image' ? null : postData.thumbnail}
          getDisplayImage={getDisplayImage}
        />
        
        {/*   */}
        <PostLinks
          links={postData.links}
          sourceUrl={postData.sourceUrl}
        />

        {/*   */}
        <PostAttachments files={postData.files} />

        {/*  */}
        <PostKeywords keywords={postData.keywords} />

        {/*   */}
        {!previewData && (
          <>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, px: 4, pt: 4 }}>
              <Typography variant="h6" color="text.secondary" gutterBottom>
                 {postData.commentCount || 0}
              </Typography>
            </Box>
            
            <Box sx={{ 
              px: 4, 
              py: 4,
              borderTop: '1px solid',
              borderColor: 'divider'
            }}>
              <Comments
                postId={postId}
                collectionName={collectionName}
                postAuthorId={postData?.authorId}
              />
            </Box>
          </>
        )}
      </Paper>
    </Container>
  );
};

export default BasePostView;