import React, { useState } from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import Chip from '@mui/material/Chip';
import Paper from '@mui/material/Paper';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import LockIcon from '@mui/icons-material/Lock';
import { useAuth } from '../auth/AuthContext';

const roleConfig = {
  STUDENT: { label: '', color: 'primary' },
  PROFESSOR: { label: '', color: 'secondary' },
  COMPANY: { label: '', color: 'success' },
  ADMIN: { label: '', color: 'error' }
};

const CommentInput = ({ 
  onSubmit, 
  isReply, 
  parentIsPrivate
}) => {
  const { currentUser } = useAuth();
  const [content, setContent] = useState('');
  const [isPrivate, setIsPrivate] = useState(false);

  //    
  const isPrivateDisabled = isReply && parentIsPrivate;
  const effectiveIsPrivate = isPrivateDisabled ? true : isPrivate;

  const handleSubmit = (e) => {
    e.preventDefault();
    if (content.trim()) {
      onSubmit(content, effectiveIsPrivate);
      setContent('');
      if (!isPrivateDisabled) {
        setIsPrivate(false);
      }
    }
  };

  return (
    <Paper sx={{ p: 2, mb: 2 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, gap: 1 }}>
        <Avatar 
          sx={{ width: 32, height: 32 }}
          src={currentUser?.profileImage || null}
        >
          {(currentUser?.displayName?.[0] || '?').toUpperCase()}
        </Avatar>
        <Typography variant="subtitle2">
          {currentUser ? (currentUser.displayName || '') : ''}
        </Typography>
        {currentUser?.role && (
          <Chip
            label={roleConfig[currentUser.role]?.label || currentUser.role}
            color={roleConfig[currentUser.role]?.color || 'default'}
            size="small"
            sx={{ height: 20 }}
          />
        )}
      </Box>

      <form onSubmit={handleSubmit}>
        <TextField
          fullWidth
          multiline
          minRows={2}
          maxRows={4}
          value={content}
          onChange={(e) => setContent(e.target.value)}
          placeholder={isReply ? " ..." : " ..."}
          size="small"
          sx={{ mb: 1 }}
        />
        <Box sx={{ 
          display: 'flex', 
          justifyContent: 'space-between',
          alignItems: 'center'
        }}>
          <Tooltip title={
            isPrivateDisabled 
              ? "    " 
              : "       "
          }>
            <FormControlLabel
              control={
                <Checkbox
                  checked={effectiveIsPrivate}
                  onChange={(e) => setIsPrivate(e.target.checked)}
                  size="small"
                  disabled={isPrivateDisabled}
                  icon={<LockIcon sx={{ fontSize: 20, color: 'text.secondary' }} />}
                  checkedIcon={<LockIcon sx={{ fontSize: 20 }} />}
                />
              }
              label={
                <Typography 
                  variant="body2" 
                  color={isPrivateDisabled ? "text.disabled" : "text.secondary"}
                >
                   {isReply ? '' : ''}
                </Typography>
              }
            />
          </Tooltip>
          <Button 
            type="submit" 
            variant="contained" 
            size="small"
            disabled={!content.trim()}
          >
            {isReply ? ' ' : ' '}
          </Button>
        </Box>
      </form>
    </Paper>
  );
};

export default CommentInput;