// src/components/comments/Comment.js   

import React, { useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Tooltip from '@mui/material/Tooltip';
import Chip from '@mui/material/Chip';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';

import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import CancelIcon from '@mui/icons-material/Cancel';
import LockIcon from '@mui/icons-material/Lock';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import NotificationImportantIcon from '@mui/icons-material/NotificationImportant';
import useReplies from '../../hooks/useReplies';
import ReportDialog from '../report/ReportDialog';

const roleConfig = {
  STUDENT: { label: '', color: 'primary' },
  PROFESSOR: { label: '', color: 'secondary' },
  COMPANY: { label: '', color: 'success' },
  ADMIN: { label: '', color: 'error' }
};

const Comment = ({ 
  id,
  author, 
  content, 
  timestamp, 
  onEdit, 
  onDelete, 
  onReply, 
  isEditable, 
  isReply,
  isPrivate,
  currentUser,
  postAuthorId,
  postId,
  collectionName,
  authorRole
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [isReplying, setIsReplying] = useState(false);
  const [showReplies, setShowReplies] = useState(false);
  const [editedContent, setEditedContent] = useState(content);
  const [replyContent, setReplyContent] = useState('');
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [reportDialogOpen, setReportDialogOpen] = useState(false);

  //   
  const isAdmin = currentUser?.role === 'ADMIN';
  //      (   true  )
  const canDelete = isEditable || isAdmin;
  const displayRole = authorRole || author?.role;

  const {
    replyList,
    replyCount,
    loadingReplies,
    hasMoreReplies,
    error,
    loadInitialReplies,
    loadMoreReplies,
    editReply,
    deleteReply
  } = useReplies(id, postId, collectionName);

  //  
  const canViewComment = !isPrivate || 
    (currentUser && currentUser.uid === author?.id) || 
    (currentUser && currentUser.uid === postAuthorId);

  const canReply = currentUser && !currentUser.isCommentBanned && (
    (!isPrivate && !isReply) ||
    (isPrivate && (
      currentUser.uid === postAuthorId || 
      (author && currentUser.uid === author.id)
    ))
  );

  //     
  if (!canViewComment) {
    return (
      <Card sx={{ 
        mb: 1.5, 
        boxShadow: isReply ? 0 : 1,
        bgcolor: 'grey.50',
        borderRadius: '8px'
      }}>
        <CardContent sx={{ py: 1.5, px: 2, '&:last-child': { pb: 1.5 } }}>
          <Box sx={{ display: 'flex', alignItems: 'center', color: 'text.secondary' }}>
            <LockIcon sx={{ mr: 1, fontSize: 20 }} />
            <Typography variant="body2"> .</Typography>
          </Box>
        </CardContent>
      </Card>
    );
  }

  const handleEdit = () => {
    setIsEditing(true);
    setEditedContent(content);
  };

  const handleCancel = () => {
    setIsEditing(false);
    setEditedContent(content);
  };

  const handleSave = () => {
    if (editedContent.trim()) {
      if (isReply) {
        onEdit(editedContent);
      } else {
        onEdit(editedContent);
      }
      setIsEditing(false);
    }
  };

  const handleDelete = () => {
    onDelete(id);
  };

  const handleShowReplies = async () => {
    if (!showReplies) {
      await loadInitialReplies();
    }
    setShowReplies(!showReplies);
  };

  const handleReply = async () => {
    if (!currentUser) {
      alert('   .');
      return;
    }
    
    if (currentUser.isCommentBanned === true) {
      alert('   .');
      return;
    }
    
    if (replyContent.trim()) {
      await onReply(id, replyContent, isPrivate);
      setReplyContent('');
      setIsReplying(false);
      
      if (!showReplies) {
        setShowReplies(true);
      }
      await loadInitialReplies();
    }
  };

  //    
  const handleOpenDeleteDialog = () => {
    setDeleteDialogOpen(true);
  };

  //  
  const handleConfirmDelete = () => {
    onDelete();
    setDeleteDialogOpen(false);
  };

  //  
  const handleReport = () => {
    setReportDialogOpen(true);
  };

  return (
    <Card sx={{ 
      mb: 1.5, 
      boxShadow: isReply ? 0 : 1,
      bgcolor: isReply ? 'grey.50' : 'white',
      borderRadius: '8px'
    }}>
      <CardContent sx={{ py: 1.5, px: 2, '&:last-child': { pb: 1.5 } }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 0.5 }}>
          <Avatar 
            sx={{ width: 28, height: 28, mr: 1.5 }}
            src={author?.profileImage || null}
          >
            {(author?.displayName?.[0] || '?').toUpperCase()}
          </Avatar>
          <Box sx={{ flexGrow: 1 }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                {author?.displayName || ''}
              </Typography>
              {author?.role && (
                <Chip
                  label={roleConfig[displayRole]?.label || displayRole}
                  color={roleConfig[displayRole]?.color || 'default'}
                  size="small"
                  sx={{ height: 20 }}
                />
              )}
              {isPrivate && (
                <Tooltip title=" ">
                  <LockIcon sx={{ fontSize: 16, color: 'warning.main' }} />
                </Tooltip>
              )}
            </Box>
            <Typography variant="caption" color="text.secondary">
              {(timestamp instanceof Date) ? 
                timestamp.toLocaleString('ko-KR', { 
                  year: 'numeric', 
                  month: 'long', 
                  day: 'numeric', 
                  hour: '2-digit', 
                  minute: '2-digit' 
                }) : 
                ' '}
            </Typography>
          </Box>
          
          {/*  */}
          {!isEditing && (
            <Box>
              {/*   -      */}
              {currentUser && currentUser.uid !== author?.id && (
                <Tooltip title="">
                  <IconButton size="small" onClick={handleReport}>
                    <NotificationImportantIcon fontSize="small" sx={{ color: 'warning.main' }} />
                  </IconButton>
                </Tooltip>
              )}
              
              {canDelete && (
                <>
                  {isEditable && (
                    <IconButton size="small" onClick={handleEdit}>
                      <EditIcon fontSize="small" />
                    </IconButton>
                  )}
                  <Tooltip title={isAdmin && !isEditable ? "  " : ""}>
                    <IconButton size="small" onClick={handleOpenDeleteDialog}>
                      {isAdmin && !isEditable ? (
                        <Box sx={{ position: 'relative' }}>
                          <DeleteIcon fontSize="small" />
                          <AdminPanelSettingsIcon 
                            sx={{ 
                              position: 'absolute', 
                              bottom: -8, 
                              right: -8, 
                              fontSize: 12, 
                              color: '#d32f2f',
                              backgroundColor: 'white',
                              borderRadius: '50%'
                            }} 
                          />
                        </Box>
                      ) : (
                        <DeleteIcon fontSize="small" />
                      )}
                    </IconButton>
                  </Tooltip>
                </>
              )}
            </Box>
          )}
          
          {isEditing && (
            <Box>
              <IconButton size="small" color="primary" onClick={handleSave}>
                <SaveIcon fontSize="small" />
              </IconButton>
              <IconButton size="small" color="error" onClick={handleCancel}>
                <CancelIcon fontSize="small" />
              </IconButton>
            </Box>
          )}
        </Box>

        {!isEditing ? (
          <Typography
            variant="body2"
            sx={{
              ml: 5,
              whiteSpace: 'pre-wrap',
              wordBreak: 'break-word'
            }}
          >
            {content}
          </Typography>
        ) : (
          <Box sx={{ ml: 5 }}>
            <TextField
              fullWidth
              multiline
              size="small"
              value={editedContent}
              onChange={(e) => setEditedContent(e.target.value)}
              sx={{ mt: 1 }}
            />
          </Box>
        )}

        {/*    */}
        {!isReply && (
          <Box sx={{ ml: 5, mt: 1, display: 'flex', gap: 1, alignItems: 'center' }}>
            {canReply && (
              <Button 
                size="small" 
                onClick={() => setIsReplying(true)}
                sx={{ 
                  minWidth: 0, 
                  px: 1,
                  color: 'text.secondary',
                  '&:hover': { bgcolor: 'grey.100' }
                }}
              >
                 
              </Button>
            )}
            {replyCount > 0 && (
              <Button
                size="small"
                onClick={handleShowReplies}
                sx={{ 
                  minWidth: 0, 
                  px: 1,
                  color: 'text.secondary',
                  '&:hover': { bgcolor: 'grey.100' }
                }}
              >
                {showReplies ? ' ' : ` ${replyCount}`}
              </Button>
            )}
          </Box>
        )}

        {error && (
          <Typography color="error" variant="caption" sx={{ ml: 5 }}>
              .
          </Typography>
        )}

        {/*   */}
        {showReplies && (
          <Box sx={{ ml: 5, mt: 1.5 }}>
            {loadingReplies ? (
              <CircularProgress size={20} sx={{ ml: 2 }} />
            ) : (
              <>
                {replyList.map(reply => (
                  <Comment
                    key={reply.id}
                    {...reply}
                    timestamp={reply.createdAt}
                    isReply={true}
                    isEditable={reply.author?.id === currentUser?.uid}
                    currentUser={currentUser}
                    postAuthorId={postAuthorId}
                    postId={postId}
                    collectionName={collectionName}
                    onEdit={(newContent) => editReply(reply.id, newContent)}
                    onDelete={() => deleteReply(reply.id)}
                    onReply={onReply}
                  />
                ))}
                {hasMoreReplies && (
                  <Button
                    size="small"
                    onClick={loadMoreReplies}
                    sx={{ 
                      mt: 1,
                      color: 'text.secondary',
                      '&:hover': { bgcolor: 'grey.100' }
                    }}
                  >
                      
                  </Button>
                )}
              </>
            )}
          </Box>
        )}

        {/*    */}
        {isReplying && (
          <Box sx={{ ml: 5, mt: 1.5 }}>
            <TextField
              fullWidth
              size="small"
              placeholder=" ..."
              value={replyContent}
              onChange={(e) => setReplyContent(e.target.value)}
              sx={{ mb: 1 }}
            />
            <Box sx={{ display: 'flex', gap: 1, justifyContent: 'flex-end' }}>
              <Button 
                size="small" 
                variant="outlined" 
                onClick={() => {
                  setIsReplying(false);
                  setReplyContent('');
                }}
              >
                
              </Button>
              <Button 
                size="small" 
                variant="contained"
                onClick={handleReply}
                disabled={!replyContent.trim()}
              >
                 
              </Button>
            </Box>
          </Box>
        )}
      </CardContent>

      {/*   */}
      <ReportDialog
        open={reportDialogOpen}
        onClose={() => setReportDialogOpen(false)}
        reportType="COMMENT"
        targetId={id}
        targetUserId={author?.id}
        targetTitle={content?.substring(0, 50) + (content?.length > 50 ? '...' : '')}
      />

      {/*    */}
      <Dialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
      >
        <DialogTitle>
          {isAdmin && !isEditable ? "  " : " "}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {isAdmin && !isEditable ? (
              <>
                <Typography variant="body1" gutterBottom>
                      ?
                </Typography>
                <Typography color="error" variant="body2">
                        .
                </Typography>
              </>
            ) : (
              "  ?     ."
            )}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>
            
          </Button>
          <Button 
            onClick={handleConfirmDelete}
            color="error"
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </Card>
  );
};

export default Comment;