import { useState, useEffect } from 'react';
import { 
  collection, 
  query, 
  where, 
  orderBy, 
  addDoc, 
  updateDoc,
  deleteDoc,
  doc,
  limit,
  startAfter,
  getDocs,
  writeBatch,
  serverTimestamp,
  getDoc,
  increment
} from 'firebase/firestore';
import { db, functions } from '../firebase';
import { useAuth } from '../components/auth/AuthContext';
import { httpsCallable } from 'firebase/functions';

const commentsHook = (postId, collectionName) => {
  const [comments, setComments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { currentUser, updateLastActivity } = useAuth();
  const [lastDoc, setLastDoc] = useState(null);  
  const [hasMore, setHasMore] = useState(true); 

  //     ()
  const checkCommentPermission = async (userId) => {
    if (!userId) return { allowed: false, message: " ." };

    try {
      //    
      const userDoc = await getDoc(doc(db, 'users', userId));
      if (!userDoc.exists()) {
        return { allowed: false, message: "    ." };
      }

      const userData = userDoc.data();

      // 1.    ()
      if (userData.isBlocked === true) {
        return { 
          allowed: false, 
          message: "       .\n   ." 
        };
      }

      // 2.   
      if (userData.isCommentBanned === true) {
        return { 
          allowed: false, 
          message: "     .\n   ." 
        };
      }

      // 3.     
      if (userData.role === 'DEFAULT') {
        return { 
          allowed: false, 
          message: "      ." 
        };
      }

      return { allowed: true };
    } catch (error) {
      console.error("Error checking comment permission:", error);
      return { allowed: false, message: "    ." };
    }
  };


  //   
  const fetchUserData = async (authorId) => {
    try {
      const userDoc = await getDoc(doc(db, 'users', authorId));
      if (userDoc.exists()) {
        return {
          id: userDoc.id,
          ...userDoc.data()
        };
      }
      return null;
    } catch (err) {
      console.error("Error fetching user data:", err);
      return null;
    }
  };

  //  
  useEffect(() => {
    const fetchComments = async () => {
      if (!postId || !collectionName) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);

        const commentsRef = collection(db, `${collectionName}_comments`);
        const q = query(
          commentsRef,
          where("postId", "==", postId),
          where("parentId", "==", null),
          orderBy("createdAt", "desc"),
          limit(10)
        );

        const snapshot = await getDocs(q);

        //      
        const commentsData = await Promise.all(
          snapshot.docs.map(async (doc) => {
            const data = doc.data();
            const author = await fetchUserData(data.authorId);
            
            return {
              id: doc.id,
              ...data,
              author,
              createdAt: data.createdAt?.toDate?.() || new Date(),
              updatedAt: data.updatedAt?.toDate?.() || new Date()
            };
          })
        );

        setComments(commentsData);
        setLastDoc(snapshot.docs[snapshot.docs.length - 1]);
        setHasMore(snapshot.docs.length === 10);
        setLoading(false);
      } catch (err) {
        console.error("Error fetching comments:", err);
        setError(err);
        setLoading(false);
      }
    };

    fetchComments();
  }, [postId, collectionName]);

  const updateCommentCount = async (postId, increment = true) => {
    const postRef = doc(db, collectionName, postId);
    await updateDoc(postRef, {
      commentCount: increment(increment ? 1 : -1)
    });
  };
  
  //  
  const addComment = async (content, isPrivate = false) => {
  if (!currentUser) throw new Error(" .");
  
  //    
  const permission = await checkCommentPermission(currentUser.uid);
  if (!permission.allowed) {
    throw new Error(permission.message);
  }
  
  try {
    //  
    if (updateLastActivity) {
      await updateLastActivity(currentUser.uid);
    }

    const batch = writeBatch(db);
    const commentsRef = collection(db, `${collectionName}_comments`);
    const newCommentRef = doc(commentsRef);

    const newComment = {
      postId,
      content,
      isPrivate,
      authorId: currentUser.uid,
      parentId: null,
      authorRole: currentUser.currentRole || currentUser.role,
      createdAt: serverTimestamp(),
      updatedAt: serverTimestamp(),
      collectionName
    };
    
    batch.set(newCommentRef, newComment);

    const postRef = doc(db, collectionName, postId);
    batch.update(postRef, {
      commentCount: increment(1)
    });
  
    await batch.commit();
    const author = await fetchUserData(currentUser.uid);

    //      
    setComments(prev => [{
      id: newCommentRef.id,
      ...newComment,
      author,
      createdAt: new Date(),
      updatedAt: new Date()
    }, ...prev]);

    } catch (err) {
      console.error("Error adding comment:", err);
      setError(err);
      throw err;
    }
  };

  const addReply = async (parentId, content, parentIsPrivate) => {
    if (!currentUser) throw new Error(" .");
    
    //    
    const permission = await checkCommentPermission(currentUser.uid);
    if (!permission.allowed) {
      throw new Error(permission.message);
    }
    
    try {
      //  
      if (updateLastActivity) {
        await updateLastActivity(currentUser.uid);
      }

      const batch = writeBatch(db);

      const commentsRef = collection(db, `${collectionName}_comments`);
      const newReplyRef = doc(commentsRef);
      const newReply = {
        postId,
        content,
        isPrivate: parentIsPrivate,
        authorId: currentUser.uid,
        parentId,
        authorRole: currentUser.currentRole || currentUser.role,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
        collectionName
      };

      batch.set(newReplyRef, newReply);
    
      const postRef = doc(db, collectionName, postId);
      batch.update(postRef, {
        commentCount: increment(1)
      });
      
      await batch.commit();

      try {
        const sendCommentNotification = httpsCallable(functions, 'sendCommentNotification');
        const result = await sendCommentNotification({
          commentId: newReplyRef.id,
          postId,
          collectionName
        });
      } catch (error) {
        console.error('Error triggering notification:', error);
      }
    } catch (err) {
      console.error("Error adding reply:", err);
      setError(err);
      throw err;
    }
  };

  const deleteComment = async (commentId) => {
    if (!currentUser) throw new Error(" .");
    
    try {
      //  
      if (updateLastActivity) {
        await updateLastActivity(currentUser.uid);
      }

      const commentsRef = collection(db, `${collectionName}_comments`);
      const batch = writeBatch(db);
      
      //    
      const commentRef = doc(db, `${collectionName}_comments`, commentId);
      const commentSnap = await getDoc(commentRef);
      
      if (!commentSnap.exists()) {
        throw new Error("   .");
      }
      
      const commentData = commentSnap.data();
      const isReply = commentData.parentId !== null;
      
      if (isReply) {
        //     
        batch.delete(commentRef);
        
        //    
        const postRef = doc(db, collectionName, postId);
        batch.update(postRef, {
          commentCount: increment(-1)
        });
        
        await batch.commit();
        
        //   useReplies   
      } else {
        //      
        const repliesQuery = query(
          commentsRef,
          where("parentId", "==", commentId)
        );
        const repliesSnapshot = await getDocs(repliesQuery);
        
        const replyCount = repliesSnapshot.docs.length;
  
        repliesSnapshot.docs.forEach(replyDoc => {
          batch.delete(doc(db, `${collectionName}_comments`, replyDoc.id));
        });
        
        //   
        batch.delete(commentRef);
  
        const postRef = doc(db, collectionName, postId);
        batch.update(postRef, {
          commentCount: increment(-(1 + replyCount))
        });
        
        await batch.commit();
  
        setComments(prev => prev.filter(comment => comment.id !== commentId));
      }
    } catch (err) {
      console.error("Error deleting comment:", err);
      setError(err);
      throw err;
    }
  };
  
  //    
  const editComment = async (commentId, newContent) => {
    if (!currentUser) throw new Error(" .");
    
    try {
      //  
      if (updateLastActivity) {
        await updateLastActivity(currentUser.uid);
      }

      const commentRef = doc(db, `${collectionName}_comments`, commentId);
      const commentSnap = await getDoc(commentRef);
      
      if (!commentSnap.exists()) {
        throw new Error("   .");
      }
      
      await updateDoc(commentRef, {
        content: newContent,
        updatedAt: serverTimestamp()
      });
  
      // UI  
      const commentData = commentSnap.data();
      const isReply = commentData.parentId !== null;
      
      if (!isReply) {
        //        
        setComments(prev => prev.map(comment => 
          comment.id === commentId 
            ? { ...comment, content: newContent, updatedAt: new Date() }
            : comment
        ));
      }
      //   useReplies       
    } catch (err) {
      console.error("Error editing comment:", err);
      setError(err);
      throw err;
    }
  };

  //  
  const loadMoreComments = async () => {
    if (!lastDoc || !hasMore) return;

    try {
      const commentsRef = collection(db, `${collectionName}_comments`);
      const q = query(
        commentsRef,
        where("postId", "==", postId),
        where("parentId", "==", null),
        orderBy("createdAt", "desc"),
        startAfter(lastDoc),
        limit(10)
      );

      const snapshot = await getDocs(q);
      
      //      
      const newComments = await Promise.all(
        snapshot.docs.map(async (doc) => {
          const data = doc.data();
          const author = await fetchUserData(data.authorId);
          
          return {
            id: doc.id,
            ...data,
            author,
            createdAt: data.createdAt?.toDate?.() || new Date(),
            updatedAt: data.updatedAt?.toDate?.() || new Date()
          };
        })
      );

      setComments(prev => [...prev, ...newComments]);
      setLastDoc(snapshot.docs[snapshot.docs.length - 1]);
      setHasMore(snapshot.docs.length === 10);
    } catch (err) {
      console.error("Error loading more comments:", err);
      setError(err);
    }
  };

  return {
    comments,
    loading,
    error,
    hasMore,
    loadMoreComments,
    addComment,
    addReply,
    editComment,
    deleteComment
  };
};

export default commentsHook;