import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import DialogContentText from '@mui/material/DialogContentText';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemText from '@mui/material/ListItemText';
import Popover from '@mui/material/Popover';
import Chip from '@mui/material/Chip';
import LockIcon from '@mui/icons-material/Lock';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import ThumbUpOutlinedIcon from '@mui/icons-material/ThumbUpOutlined';
import PersonIcon from '@mui/icons-material/Person';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import NotificationImportantIcon from '@mui/icons-material/NotificationImportant';
import CloneToPortfolioButton from '../CloneToPortfolioButton';
import ReportDialog from '../../report/ReportDialog';

/**
 *    -  , , /   
 */
const PostHeader = ({
  postId,
  postData,
  authorData,
  isLiked,
  likeCount,
  isPreview,
  currentUser,
  collectionName,
  onDelete,
  onLike,
  onAuthorClick,
}) => {
  const navigate = useNavigate();
  const [showLikesDialog, setShowLikesDialog] = useState(false);
  const [likedUsers, setLikedUsers] = useState([]);
  const [loadingLikes, setLoadingLikes] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [reportDialogOpen, setReportDialogOpen] = useState(false);
  
  //    -     
  const isAdmin = currentUser?.role === 'ADMIN';
  //    
  const canEdit = currentUser?.uid === authorData?.id;
  const canDelete = canEdit || isAdmin;

  //   
  const isSoftconProject = collectionName === 'softcon_projects';

  //   
  const handleOpenLikes = () => {
    if (likeCount > 0) {  //     
      setShowLikesDialog(true);
      fetchLikedUsers();
    }
  };

  const handleCloseLikes = () => {
    setShowLikesDialog(false);
  };

  const fetchLikedUsers = async () => {
    try {
      setLoadingLikes(true);
      setLoadingLikes(false);
    } catch (error) {
      console.error('Error fetching liked users:', error);
      setLoadingLikes(false);
    }
  };

  const handleEdit = () => {
    navigate(`/${collectionName}/${postId}/edit`);
  };

  const handleDeleteClick = () => {
    setDeleteDialogOpen(true);
  };

  //  
  const handleDelete = async () => {
    if (deleting) return;
    
    try {
      setDeleting(true);
      await onDelete();
      setDeleteDialogOpen(false);
    } catch (error) {
      console.error('Error deleting post:', error);
    } finally {
      setDeleting(false);
    }
  };

  //    
  const handleUserClick = (userId) => {
    handleCloseLikes();
    if (currentUser?.uid === userId) {
      navigate('/mypage');
    } else {
      navigate(`/profile/${userId}`);
    }
  };

  //  
  const handleReport = () => {
    setReportDialogOpen(true);
  };

  return (
    <>
      {postData.isPublic === false && (
        <Chip 
          label="" 
          color="default" 
          size="small" 
          icon={<LockIcon />} 
          sx={{ ml: 1 }}
        />
      )}
      
      {/*      */}
      <Box sx={{ 
        p: 3, 
        display: 'flex', 
        justifyContent: 'space-between',
        alignItems: 'center',
        borderBottom: '1px solid',
        borderColor: 'divider'
      }}>
        <Box 
          onClick={onAuthorClick}
          sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            gap: 2,
            cursor: 'pointer',
            '&:hover': { opacity: 0.8 }
          }}
        >
          <Avatar 
            src={authorData?.profileImage} 
            alt={authorData?.displayName}
            sx={{ width: 48, height: 48 }}
          >
            {!authorData?.profileImage && authorData?.displayName?.[0]}
          </Avatar>
          <Box>
            <Typography variant="subtitle1" sx={{ fontWeight: 500 }}>
              {authorData?.displayName || ''}
            </Typography>
            <Typography variant="caption" color="text.secondary">
              {authorData?.role === 'STUDENT' ? '' : 
                authorData?.role === 'COMPANY' ? '' :
                authorData?.role === 'PROFESSOR' ? '' : ''}
            </Typography>
          </Box>
        </Box>

        {!isPreview && (
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            {isSoftconProject && currentUser?.role === 'STUDENT' && (
              <CloneToPortfolioButton postData={postData} postId={postId} />
            )}
            
            {/*   -      */}
            {currentUser && currentUser.uid !== authorData?.id && (
              <Tooltip title="">
                <IconButton
                  onClick={handleReport}
                  sx={{ 
                    color: 'warning.main',
                    '&:hover': {
                      backgroundColor: 'rgba(255, 152, 0, 0.04)'
                    }
                  }}
                >
                  <NotificationImportantIcon />
                </IconButton>
              </Tooltip>
            )}
            
            {/*  /  ,     */}
            {canDelete && (
              <Box sx={{ display: 'flex', gap: 1 }}>
                {/*      */}
                {canEdit && (
                  <Tooltip title="">
                    <IconButton
                      onClick={handleEdit}
                      sx={{ 
                        color: 'rgb(0, 51, 161)',
                        '&:hover': {
                          backgroundColor: 'rgba(0, 51, 161, 0.04)'
                        }
                      }}
                    >
                      <EditIcon />
                    </IconButton>
                  </Tooltip>
                )}
                
                {/*        */}
                <Tooltip title={isAdmin && !canEdit ? "  " : ""}>
                  <IconButton
                    onClick={handleDeleteClick}
                    sx={{ 
                      color: '#d32f2f',
                      '&:hover': {
                        backgroundColor: 'rgba(211, 47, 47, 0.04)'
                      }
                    }}
                  >
                    {isAdmin && !canEdit ? (
                      <Box sx={{ position: 'relative' }}>
                        <DeleteIcon />
                        <AdminPanelSettingsIcon 
                          sx={{ 
                            position: 'absolute', 
                            bottom: -8, 
                            right: -8, 
                            fontSize: 16, 
                            color: '#d32f2f',
                            backgroundColor: 'white',
                            borderRadius: '50%'
                          }} 
                        />
                      </Box>
                    ) : (
                      <DeleteIcon />
                    )}
                  </IconButton>
                </Tooltip>
              </Box>
            )}
            
            {/*    */}
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              {currentUser ? (
                <Tooltip title={isLiked ? ' ' : ''}>
                  <IconButton 
                    onClick={onLike}
                    sx={{ 
                      color: isLiked ? 'rgb(0, 51, 161)' : 'grey.500',
                      '&:hover': { bgcolor: 'rgba(0, 51, 161, 0.04)' }
                    }}
                  >
                    {isLiked ? <ThumbUpIcon /> : <ThumbUpOutlinedIcon />}
                  </IconButton>
                </Tooltip>
              ) : (
                <Tooltip title=" ">
                  <span>
                    <IconButton disabled>
                      <ThumbUpOutlinedIcon />
                    </IconButton>
                  </span>
                </Tooltip>
              )}
              <Typography 
                variant="body2" 
                color="text.secondary"
                onClick={handleOpenLikes}
                className="likes-trigger"
                sx={{ 
                  cursor: 'pointer',
                  '&:hover': { 
                    color: 'rgb(0, 51, 161)',
                    textDecoration: 'underline'
                  }
                }}
              >
                {likeCount}
              </Typography>
            </Box>
          </Box>
        )}
      </Box>

      {/*   */}
      <ReportDialog
        open={reportDialogOpen}
        onClose={() => setReportDialogOpen(false)}
        reportType="POST"
        targetId={postId}
        targetUserId={authorData?.id}
        targetTitle={postData?.title}
      />

      {/*     */}
      <Popover
        open={showLikesDialog}
        onClose={handleCloseLikes}
        anchorEl={document.querySelector('.likes-trigger')}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        PaperProps={{
          sx: {
            width: 250,
            maxHeight: 300,
            overflow: 'auto',
            mt: 1,
            boxShadow: '0 4px 20px rgba(0, 0, 0, 0.15)',
            borderRadius: '12px',
            '&::-webkit-scrollbar': {
              width: '6px',
            },
            '&::-webkit-scrollbar-thumb': {
              backgroundColor: 'rgba(0, 0, 0, 0.2)',
              borderRadius: '3px',
            },
          }
        }}
      >
        {loadingLikes ? (
          <Box sx={{ p: 2, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              Loading...
            </Typography>
          </Box>
        ) : likedUsers.length === 0 ? (
          <Box sx={{ p: 2, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
                 .
            </Typography>
          </Box>
        ) : (
          <List sx={{ pt: 1, pb: 1 }}>
            {likedUsers.map((user) => (
              <ListItem 
                button 
                onClick={() => handleUserClick(user.id)}
                key={user.id}
                sx={{ 
                  px: 2,
                  '&:hover': {
                    backgroundColor: 'rgba(0, 51, 161, 0.04)'
                  }
                }}
              >
                <ListItemAvatar>
                  <Avatar 
                    src={user.profileImage}
                    alt={user.displayName}
                    sx={{ width: 32, height: 32 }}
                  >
                    {!user.profileImage && <PersonIcon sx={{ fontSize: 20 }} />}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText 
                  primary={user.displayName}
                  primaryTypographyProps={{
                    variant: 'body2',
                    sx: { fontWeight: 500 }
                  }}
                />
              </ListItem>
            ))}
          </List>
        )}
      </Popover>

      {/*    */}
      <Dialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
        aria-labelledby="delete-dialog-title"
        aria-describedby="delete-dialog-description"
      >
        <DialogTitle id="delete-dialog-title">
          {isAdmin && !canEdit ? "  " : " "}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="delete-dialog-description">
            {isAdmin && !canEdit ? (
              <>
                <Typography variant="body1" gutterBottom>
                      ?
                </Typography>
                <Typography color="error" variant="body2">
                        .
                </Typography>
              </>
            ) : (
              "   ?     ."
            )}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => setDeleteDialogOpen(false)}
            disabled={deleting}
          >
            
          </Button>
          <Button 
            onClick={handleDelete} 
            color="error"
            disabled={deleting}
            autoFocus
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default PostHeader;