import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  doc, 
  deleteDoc, 
  collection, 
  query, 
  where, 
  getDocs, 
  writeBatch 
} from 'firebase/firestore';
import { ref, deleteObject } from 'firebase/storage';
import { db, storage } from '../firebase';

/**
 *  (/ )   
 * @param {string} postId -  ID
 * @param {string} collectionName -   (portfolios, labs, companies)
 * @param {Object} postData -   
 */
const usePostActions = (postId, collectionName, postData) => {
  const navigate = useNavigate();
  const [deleting, setDeleting] = useState(false);
  const [error, setError] = useState(null);

  //    
  const handleEdit = () => {
    navigate(`/${collectionName}/${postId}/edit`);
  };

  //   
  const handleDelete = async () => {
    if (!postId || deleting) return;
    
    try {
      setDeleting(true);
      setError(null);

      const batch = writeBatch(db);
      
      // 1.  
      const commentsRef = collection(db, `${collectionName}_comments`);
      const commentsQuery = query(commentsRef, where("postId", "==", postId));
      const commentsSnapshot = await getDocs(commentsQuery);
      commentsSnapshot.docs.forEach((doc) => {
        batch.delete(doc.ref);
      });

      // 2.  
      const likesRef = collection(db, 'likes');
      const likesQuery = query(likesRef, where("postId", "==", postId));
      const likesSnapshot = await getDocs(likesQuery);
      likesSnapshot.docs.forEach((doc) => {
        batch.delete(doc.ref);
      });

      // 3.   ()
      if (postData?.files) {
        for (const file of postData.files) {
          if (file.url) {
            try {
              const fileRef = ref(storage, file.url);
              await deleteObject(fileRef);
            } catch (fileError) {
              console.error('Error deleting file:', fileError);
              //     
            }
          }
        }
      }

      // 4.   ()
      if (postData?.thumbnail) {
        try {
          const thumbnailRef = ref(storage, postData.thumbnail);
          await deleteObject(thumbnailRef);
        } catch (thumbnailError) {
          console.error('Error deleting thumbnail:', thumbnailError);
          //     
        }
      }

      // 5.   
      const postRef = doc(db, collectionName, postId);
      batch.delete(postRef);

      //   
      await batch.commit();

      //     
      navigate(`/${collectionName}`);
      return true;
    } catch (err) {
      console.error('Error deleting post:', err);
      setError('    .');
      return false;
    } finally {
      setDeleting(false);
    }
  };

  return {
    handleEdit,
    handleDelete,
    deleting,
    error
  };
};

export default usePostActions;