import React from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Alert from '@mui/material/Alert';
import { useAuth } from '../auth/AuthContext';
import CommentInput from './CommentInput';
import CommentList from './CommentList';
import commentsHook from '../../hooks/commentsHook';

const Comments = ({ postId, collectionName, postAuthorId }) => {
  const { currentUser } = useAuth();
  const { 
    comments, 
    loading, 
    error,
    hasMore,
    loadMoreComments,
    addComment, 
    addReply,
    editComment, 
    deleteComment 
  } = commentsHook(postId, collectionName);

  if (loading) {
    return (
      <Box sx={{ textAlign: 'center', py: 4 }}>
        <Typography color="text.secondary">
            ...
        </Typography>
      </Box>
    );
  }

  if (collectionName === 'softcon_projects') {
    return (
      <Typography 
        color="text.secondary" 
        textAlign="center" 
        sx={{ py: 4 }}
      >
           .
      </Typography>
    );
  }

  if (error) {
    return (
      <Alert severity="error" sx={{ mt: 2 }}>
          .
      </Alert>
    );
  }

  //    
  const getCommentPermissionMessage = () => {
    if (!currentUser) {
      return { type: 'info', message: '   .' };
    }

    //    ( )
    if (currentUser.isBlocked === true) {
      return { 
        type: 'error', 
        message: '      .\n   .' 
      };
    }

    //   
    if (currentUser.isCommentBanned === true) {
      return { 
        type: 'warning', 
        message: '   .\n   .' 
      };
    }

    //     
    if (currentUser.role === 'DEFAULT') {
      return { 
        type: 'info', 
        message: '      .' 
      };
    }

    //      
    return null;
  };

  const permissionMessage = getCommentPermissionMessage();

  return (
    <div>
      {permissionMessage ? (
        <Alert severity={permissionMessage.type} sx={{ mb: 2 }}>
          {permissionMessage.message}
        </Alert>
      ) : (
        <CommentInput onSubmit={addComment} />
      )}
      
      <CommentList
        comments={comments}
        onEdit={editComment}
        onDelete={deleteComment}
        onReply={addReply}
        currentUser={currentUser}
        postAuthorId={postAuthorId} 
        hasMore={hasMore}
        onLoadMore={loadMoreComments}
        postId={postId}  
        collectionName={collectionName} 
      />
    </div>
  );
};

export default Comments;